<template>
    <v-container class="pa-0">
        <!-- Header -->
        <PDHeader></PDHeader>

        <!-- Main -->
        <v-main>
            <!-- 衛教表單分類 -->
            <v-tabs color="blue" v-model="tab">
                <v-tab value="false">未完成</v-tab>
                <v-tab value="true">已完成</v-tab>
            </v-tabs>


            <v-table density="compact" class="mt-1">
                <thead>
                    <tr>
                        <th class="text-center">
                            日期
                        </th>
                        <th class="text-center">
                            衛教項目
                        </th>
                        <th class="text-center">
                            評值
                        </th>
                        <th class="text-center">
                            表單連結
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in selected_list" :key="item.teach_name">
                        <td class="text-center">{{ item.teach_date }}</td>
                        <td class="text-center">{{ item.teach_name }}</td>
                        <td class="text-center"><v-icon :icon="seletced_complete"></v-icon></td>
                        <td class="text-center" style="font-size: 18px;"><v-icon icon="mdi-link"></v-icon></td>
                    </tr>
                </tbody>
            </v-table>
        </v-main>

        <!-- Footer -->
        <PDFooter></PDFooter>
    </v-container>
</template>


<script setup>
// components
import PDHeader from '@/components/PD-Header.vue'
import PDFooter from '@/components/PD-Footer.vue'
import { ref,computed } from 'vue';
import { useStore } from 'vuex';

//variable
const tab = ref('false');
const selected_list = ref(useStore().state.teach_list.filter(function (item) {
    return item.teach_complete.toString() == tab.value;
}));

const seletced_complete = computed(() => {//eslint-disable-line
    if (tab.value == 'false') {
        return 'mdi-close';
    }
    else if (tab.value == 'true') {
        return 'mdi-check';
    }
});





</script>

<style lang="scss" scoped>
thead {
    tr {}
}

tbody {
    tr {

        td {
            border-top: 1px solid black;
            border-bottom: 1px solid black;
            text-align: center;
            font-size: 14px;        
        }
    }
}
</style>